<template>
  <div>
    <!--  -->
    <input type="text" :value="postcode" placeholder="" /><br />
    <input type="button" @click="getAddress" value=" " /><br />
    <input type="text" :value="address" placeholder="" /><br />
    <input
      type="text"
      v-model="detailAddress"
      ref="detailAddress"
      placeholder=""
    /><br />
    <input type="text" :value="extraAddr" placeholder="" />
  </div>
</template>

<script>
export default {
  name: 'addressForm',
  data() {
    return {
      address: '',
      extraAddr: '',
      postcode: '',
      detailAddress: '',
    };
  },
  methods: {
    getAddress() {
      new window.daum.Postcode({
        oncomplete: (userData) => {
          this.extraAddr = '';
          if (userData.userSelectedType === 'R') {
            this.address = userData.roadAddress;
          } else {
            this.address = userData.jibunAddress;
          }
          //       .
          if (userData.userSelectedType === 'R') {
            if (userData.bname !== '' && /[||]$/g.test(userData.bname)) {
              this.extraAddr += userData.bname;
            }
            if (userData.buildingName !== '' && userData.apartment === 'Y') {
              this.extraAddr +=
                this.extraAddr !== ''
                  ? ' , ' + userData.buildingName
                  : userData.buildingName;
            }
            if (this.extraAddr !== '') {
              this.extraAddr = '(' + this.extraAddr + ')';
            }
          }
          this.postcode = userData.zonecode;
          this.$refs.detailAddress.focus();
        },
      }).open();
    },
  },
};
</script>

<style></style>
