<template>
  <div>
    <h1> !</h1>
    <form @submit.prevent="onSubmit()">
      <fieldset>
        <!--  -->
        <input 
          v-model="credentials.name" 
          type="text" 
          placeholder=""
        />
        <!--  -->
        <input
          v-model="credentials.userpwd"
          type="password"
          minlength="8"
          placeholder=" "
          autocomplete="new-password"
        />
        <!--   -->
        <input
          v-model="userpwdconfirmation"
          type="password"
          minlength="8"
          placeholder=" "
          autocomplete="new-password"
        />
        <!--  -->
        <input
          v-model="credentials.phone"
          type="tel"
          pattern="[0-9]{3}-[0-9]{4}-[0-9]{4}"
          placeholder=""
          
        />
        <!--   -->
        <div>  : {{ credentials.address }} </div> 
        <!--  -->
        <addressForm class="addressForm"
        ref="addressForm"
        />
        <input
          type="submit" 
          value="  "
        />
      </fieldset>
    </form>
  </div>
</template>

<script>
// useStore   store .
import addressForm from '@/components/User/Join/addressForm'
import { useStore } from "vuex";

export default {
  // vuex 
  setup () {
      const store = useStore();
      return { store }
  },
  name: "UserModify",
  components: {
    addressForm
  },
  data() {
    return {
      credentials: {
        //   
        name: "",
        phone: "",
        address: "",
        type: ""
      },
      userpwdconfirmation: "",
    }
  },
  //    local 
  // ,  vuex 
  methods: {
    onSubmit() {
      if (this.issamepassword) {
        this.updateAddress()
        //  
        this.store.dispatch("userStore/register", this.credentials)
      } else {
        this.resetPassword()
        alert("  !")
      }
    },
    //   
    resetPassword() {
        this.credentials.userpwd = ""
        this.userpwdconfirmation = ""
    },
    //  
    updateAddress() {
      let address = ""
      //  
      address += this.$refs.addressForm.address
      // 
      if (this.$refs.addressForm.detailAddress) {
        address += ' ' + this.$refs.addressForm.detailAddress
      // 
      address += ' ' + this.$refs.addressForm.extraAddr
      // 
      address += ' ' + this.$refs.addressForm.postcode
      this.credentials.address = address
      }
    }
  },
  computed: {
    issamepassword() {
      return Boolean(this.credentials.userpwd === this.userpwdconfirmation)
    },
  },
  created() {
    
  }
}
</script>
<style scoped>
  fieldset {
    display: flex;
    flex-direction: column;
    text-align: center;
    border-radius: 1rem;
  }

  fieldset * {
    margin-top: 0.5rem;
    padding: 0.5rem;
  }

  fieldset input {
    border: 1px solid black;
    border-radius: 1rem;
    margin: 1rem 2rem;
  }

  fieldset input:focus {
    outline:none;
  }

  .addressForm {
    display: flex;
  }

</style>
