<template>
  <div>
    <!-- ========================  ======================== -->
    <h2></h2>
    <table class="infoTable">
      <tr>
        <th></th>
        <td>{{ user.member_name }}</td>
      </tr>
      <tr>
        <th></th>
        <td>{{ user.member_userid }}</td>
      </tr>
      <tr>
        <th></th>
        <td>{{ user.member_phone }}</td>
      </tr>
    </table>
    <!-- ========================  ======================== -->
    <h2></h2>
    <!--    trigger  -->
    <button
      type="button"
      class="btn btn-primary"
      data-bs-toggle="modal"
      data-bs-target="#add"
      v-if="addressBook.length == 0"
    >
       
    </button>
    <!--  ( )  trigger  -->
    <button
      type="button"
      class="btn btn-primary"
      data-bs-toggle="modal"
      data-bs-target="#select"
      v-if="addressBook.length != 0"
    >
       
    </button>
    <!--   -->
    <p v-if="addressBook.length == 0">
        .  
    </p>
    <table class="infoTable" v-if="addressBook.length != 0">
      <tr>
        <th>()</th>
        <td>{{ receiverInfo.name }}</td>
      </tr>
      <tr>
        <th></th>
        <td>{{ receiverInfo.address }}</td>
      </tr>
      <tr>
        <th></th>
        <td>{{ receiverInfo.phone }}</td>
      </tr>
      <tr>
        <th> </th>
        <td>{{ receiverInfo.request }}</td>
      </tr>
    </table>

    <!-- ======================== Modal ======================== -->
    <!-- ======================== Modal -   ======================== -->
    <div
      class="modal fade"
      id="select"
      aria-hidden="true"
      aria-labelledby="exampleModalToggleLabel"
      tabindex="-1"
    >
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalToggleLabel">
               
            </h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            <table
              class="modalTable"
              :class="{ selected: receiverInfo.id == addr.id }"
              v-for="(addr, index) in addressBook"
              :key="`${index}_item`"
            >
              <tr>
                <th>{{ addr.name }}</th>
              </tr>
              <tr v-if="addr.type === 1" style="color: blue">
                
              </tr>
              <tr>
                <td>{{ addr.address }}</td>
              </tr>
              <tr>
                <td>{{ addr.phone }}</td>
              </tr>
              <tr>
                <td>{{ addr.request }}</td>
              </tr>
              <tr>
                <td>
                  <button
                    type="button"
                    class="btn btn-success"
                    style="margin-right: 5px"
                    data-bs-target="#edit"
                    data-bs-toggle="modal"
                    data-bs-dismiss="modal"
                    @click="edit(addr)"
                  >
                    
                  </button>
                  <button
                    type="button"
                    class="btn btn-primary"
                    @click="updateReceiver(addr)"
                    data-bs-dismiss="modal"
                  >
                    
                  </button>
                </td>
              </tr>
            </table>
          </div>
          <div class="modal-footer">
            <button
              class="btn btn-primary"
              data-bs-target="#add"
              data-bs-toggle="modal"
              data-bs-dismiss="modal"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- ======================== Modal -   ======================== -->
    <div
      class="modal fade"
      id="add"
      aria-hidden="true"
      aria-labelledby="exampleModalToggleLabel"
      tabindex="-1"
    >
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalToggleLabel">
               
            </h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            <!--  -->
            <form>
              <fieldset>
                <!--  -->
                <input
                  v-model="input.name"
                  type="text"
                  placeholder=" "
                  style="width: 100%"
                />
                <!--  -->
                <div style="width: 100%; margin-bottom: 10px">
                  <input
                    type="text"
                    v-model="postcode"
                    placeholder=""
                  />
                  <input
                    type="button"
                    @click="execDaumPostcode()"
                    value=" "
                  /><br />
                  <input
                    type="text"
                    v-model="address"
                    placeholder=""
                    style="width: 100%"
                  /><br />
                  <input
                    type="text"
                    v-model="detailAddress"
                    placeholder=""
                    style="width: 50%"
                  />
                  <input
                    type="text"
                    v-model="extraAddr"
                    placeholder=""
                    style="width: 50%"
                  />
                </div>
                <!--  -->
                <input
                  v-model="input.phone"
                  type="tel"
                  pattern="[0-9]{3}-[0-9]{4}-[0-9]{4}"
                  placeholder=""
                  style="width: 100%"
                />
                <!--  -->
                <input
                  v-model="input.request"
                  type="text"
                  placeholder=" "
                  style="width: 100%"
                />
                <div>
                  <label for="saveAsDefault">
                    <input
                      id="saveAsDefault"
                      type="checkbox"
                      v-model="input.isBasic"
                    />
                    <span> </span>
                  </label>
                </div>
              </fieldset>
            </form>
          </div>
          <div class="modal-footer">
            <button
              class="btn btn-primary"
              data-bs-target="#select"
              data-bs-toggle="modal"
              data-bs-dismiss="modal"
              @click="save(user.member_userid)"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- ======================== Modal -   ======================== -->
    <div
      class="modal fade"
      id="edit"
      aria-hidden="true"
      aria-labelledby="exampleModalToggleLabel"
      tabindex="-1"
    >
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalToggleLabel">
               
            </h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            <!--  -->
            <form>
              <fieldset>
                <!--  -->
                <input
                  v-model="addressInEdit.name"
                  type="text"
                  placeholder=" "
                  style="width: 100%"
                />
                <!--  -->
                <div style="width: 100%; margin-bottom: 10px">
                  <input
                    type="text"
                    v-model="postcode"
                    placeholder=""
                  />
                  <input
                    type="button"
                    @click="execDaumPostcode()"
                    value=" "
                  /><br />
                  <input
                    type="text"
                    v-model="address"
                    placeholder=""
                    style="width: 100%"
                  /><br />
                  <input
                    type="text"
                    v-model="detailAddress"
                    placeholder=""
                    style="width: 50%"
                  />
                  <input
                    type="text"
                    v-model="extraAddr"
                    placeholder=""
                    style="width: 50%"
                  />
                </div>
                <!--  -->
                <input
                  v-model="addressInEdit.phone"
                  type="tel"
                  pattern="[0-9]{3}-[0-9]{4}-[0-9]{4}"
                  placeholder=""
                  style="width: 100%"
                />
                <!--  -->
                <input
                  v-model="addressInEdit.request"
                  type="text"
                  placeholder=" "
                  style="width: 100%"
                />
                <div>
                  <label for="saveAsDefault">
                    <input
                      id="saveAsDefault"
                      type="checkbox"
                      v-model="checked"
                    />
                    <span> </span>
                  </label>
                </div>
              </fieldset>
            </form>
          </div>
          <div class="modal-footer">
            <button
              class="btn btn-primary"
              data-bs-target="#select"
              data-bs-toggle="modal"
              data-bs-dismiss="modal"
              @click="modify(addressInEdit.id)"
            >
              
            </button>
            <button
              class="btn btn-danger"
              data-bs-target="#select"
              data-bs-toggle="modal"
              data-bs-dismiss="modal"
              @click="deleteAddr(addressInEdit.id, addressInEdit.member_id)"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'customerInfo',
  data() {
    return {
      input: {
        name: '',
        phone: '',
        request: '',
        isBasic: false,
      },
      address: '',
      extraAddr: '',
      postcode: '',
      detailAddress: '',
      addressInEdit: {}, //  
    };
  },
  computed: {
    //   
    user() {
      return this.$store.state.userStore.user;
    },
    //    
    addressBook() {
      return this.$store.state.order.addressBook;
    },
    // 
    receiverInfo: {
      get() {
        return this.$store.state.order.receiverInfo;
      },
      set(value) {
        this.$store.state.order.receiverInfo = value;
      },
    },
    checked() {
      return this.addressInEdit.type == 1;
    },
  },
  methods: {
    //   .
    updateReceiver(addr) {
      // this.$store.state.order.receiverInfo = addr;
      // this.receiverInfo = addr;
      this.$store.dispatch('order/updateReceiver', addr);
    },
    // 
    execDaumPostcode() {
      new window.daum.Postcode({
        oncomplete: (userData) => {
          this.extraAddr = '';
          if (userData.userSelectedType === 'R') {
            this.address = userData.roadAddress;
          } else {
            this.address = userData.jibunAddress;
          }
          //       .
          if (userData.userSelectedType === 'R') {
            if (userData.bname !== '' && /[||]$/g.test(userData.bname)) {
              this.extraAddr += userData.bname;
            }
            if (userData.buildingName !== '' && userData.apartment === 'Y') {
              this.extraAddr +=
                this.extraAddr !== ''
                  ? ' , ' + userData.buildingName
                  : userData.buildingName;
            }
            if (this.extraAddr !== '') {
              this.extraAddr = '(' + this.extraAddr + ')';
            }
          }
          this.postcode = userData.zonecode;
        },
      }).open();
    },
    //  
    save(id) {
      //  -  
      var newAddr = {};
      newAddr.name = this.input.name;
      var fullAddr =
        this.address +
        ' ' +
        this.detailAddress +
        ' ' +
        this.extraAddr +
        ' ' +
        this.postcode;
      newAddr.address = fullAddr;
      newAddr.phone = this.input.name;
      newAddr.request = this.input.name;
      newAddr.type = this.input.isBasic ? 1 : 2;
      newAddr.member_id = id;

      //     
      if (newAddr.type == 1) {
        this.$store.dispatch('order/resetDefaultAddress');
      }

      // addressBook 
      this.$store.dispatch('order/addAddress', newAddr);

      // 
      this.input.name = '';
      this.input.phone = '';
      this.input.request = '';
      this.input.isBasic = false;
      this.input.address = '';
      this.input.extraAddr = '';
      this.input.postcode = '';
      this.input.detailAddress = '';
    },
    edit(addr) {
      this.addressInEdit = addr;
    },
    //  
    modify(addrID) {
      var addr = {};
      addr.id = addrID;
      addr.name = this.addressInEdit.name;
      addr.phone = this.addressInEdit.name;
      addr.request = this.addressInEdit.name;
      //  
      console.log(this.checked);
      if (this.checked) {
        this.$store.dispatch('order/resetDefaultAddress');
        addr.type = 1;
      } else {
        addr.type = 2;
      }
      addr.member_id = this.addressInEdit.member_id;
      //  
      var fullAddr =
        this.address +
        ' ' +
        this.detailAddress +
        ' ' +
        this.extraAddr +
        ' ' +
        this.postcode;
      addr.address = fullAddr;
      // 
      this.$store.dispatch('order/modifyAddress', addr);
    },
    deleteAddr(addrID, userid) {
      this.$store.dispatch('order/deleteAddress', {
        id: addrID,
        userid: userid,
      });
    },
  },
  created() {
    //   
    // this.$store.dispatch('order/getAddressBook', this.user.member_userid);
    //   
    this.$store.dispatch('order/initReceiverInfo');
  },
};
</script>

<style scoped>
.infoTable {
  width: 100%;
  border-top: 1px solid #333333;
  border-collapse: collapse;
  margin-bottom: 20px;
}
.infoTable > tr {
  border-bottom: 1px solid #333333;
}
.modalTable {
  width: 100%;
  border: 2px solid #333333;
}
.selected {
  width: 100%;
  border: 2px solid blue;
}
fieldset > input {
  margin-bottom: 10px;
}
</style>
