<template>
  <div>
    <h1>  !</h1>
    <form v-if="!isSubmitted" @submit.prevent="onSubmit()">
      <span>          .</span>
      <span>      </span>
      <fieldset>
        <!--  -->
        <input 
          v-model="name" 
          type="text" 
          placeholder=""
        />
        <!--  -->
        <input
          v-model="userid"
          type="email"
          placeholder="()"
          autocomplete="email"
          pattern="^[^(\.)][a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}"
        />
        <input
          type="submit" 
          value=" "
        />
      </fieldset>
    </form>
    <div v-if="isSubmitted">
      <p>
        {{ userid }}   .
            ' '   .
          ?
         ,   () Eureka   .
      </p>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'SearchPassword',
  data() {
    return {
      name: '',
      userid: '',
      isSubmitted: false,
    }
  },
  methods: {
    onSubmit() {
      console.log('')

      axios({
        methods: 'POST',
        url: 'http://localhost/member/findpwd',
        data: {
            member_userid: this.userid,
            member_name: this.name,
            // 
            member_phone: "010-1234-5678"
        }
      })
      .then((res) => {
        this.isSubmitted = true
        console.log(res)
      })
      .catch((err) => {
        console.log(err)
      })
    }
  }

}
</script>

