<template>
  <div>
    <h2> </h2>
    <!-- Card   . -->
    <div
      class="card mb-3"
      style="max-width: 540px"
      v-for="(item, index) in selectedProducts"
      :key="`${index}_item`"
    >
      <div class="row g-0">
        <div class="col-md-4">
          <img
            :src="item.product_img"
            class="img-fluid rounded-start"
            alt="  "
          />
        </div>
        <div class="col-md-8">
          <div class="card-body">
            <!-- <h5 class="card-title">{{ item.title }}</h5> -->
            <p class="card-text">
              {{ item.seller_name }}
            </p>
            <p class="card-text">
              {{ item.product_name }}
            </p>
            <p class="card-text">
              <del v-if="item.product_discount > 0">
                {{ $filters.price(item.price * item.quantity) }}
              </del>
              <b v-else> {{ $filters.price(item.price * item.quantity) }} </b>
              /
              <b> {{ item.quantity }} </b>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'payInfo',
  computed: {
    //  ( )  
    selectedProducts() {
      let items = this.$store.state.cart.items;
      let checked = this.$store.state.cart.checked;
      let selectedItems = [];

      // checked    id   push.
      checked.forEach((id) => {
        selectedItems.push(items.filter((item) => item.id === id));
      });

      return selectedItems;
    },
  },
};
</script>

