import http from '@/utils/http-common';

export const order = {
  namespaced: true,
  state: () => ({
    //    
    addressBook: [
      {
        id: 1,
        name: '',
        address: ' ',
        phone: '010-4412-2231',
        request: ' ',
        type: 1,
        member_id: 'ab@naver.com',
      },
      {
        id: 2,
        name: '',
        address: '',
        phone: '010-2222-2331',
        request: '',
        type: 2,
        member_id: 'ab@naver.com',
      },
      {
        id: 3,
        name: '',
        address: '',
        phone: '010-3333-2231',
        request: '',
        type: 2,
        member_id: 'ab@naver.com',
      },
    ],
    // (     )
    receiverInfo: {},
  }),
  getters: {},
  mutations: {
    //    state 
    GET_ADDRESS_BOOK(state, list) {
      state.addressBook = list;
    },
    //   
    INIT_RECEIVER_INFO(state) {
      let size = state.addressBook.length;
      if (size != 0) {
        // let basicAddress = state.addressBook.filter((addr) => addr.type === 1); //   
        for (let i = 0; i < size; i++) {
          if (state.addressBook[i].type == 1) {
            state.receiverInfo = state.addressBook[i];
            return;
          }
        }
        state.receiverInfo = state.addressBook[0]; //      
      }
    },
    //   
    UPDATE_RECEIVER(state, addr) {
      state.receiverInfo = addr;
    },
    //   ->   
    RESET_DEFAULT_ADDRESS(state) {
      //      
      for (let i = 0; i < state.addressBook.length; i++) {
        if (state.addressBook[i].type == 1) {
          state.addressBook[i].type = 2;
          break;
        }
      }
    },
  },
  actions: {
    //  id    
    getAddressBook({ commit }, id) {
      // @@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@ TODO:   url 
      http
        .get('--', id)
        .then((response) => {
          //  status 
          if (response.status == 200) {
            // 
            commit('GET_ADDRESS_BOOK', response.data);
          } else {
            // 
          }
        })
        .catch((error) => {
          console.dir(error);
        });
    },
    //   .    
    initReceiverInfo({ commit }) {
      commit('INIT_RECEIVER_INFO');
    },
    //   
    updateReceiver({ commit }, addr) {
      commit('UPDATE_RECEIVER', addr);
    },
    //   ->   
    resetDefaultAddress({ commit }) {
      commit('RESET_DEFAULT_ADDRESS');
    },
    //   
    addAddress(addr) {
      // @@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@ TODO:   url 
      http
        .post('--', addr)
        .then((response) => {
          if (response.status == 200) {
            //  addressBook 
            this.getAddressBook(addr.member_id);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    //   
    modifyAddress(addr) {
      // @@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@ TODO:   url 
      http
        .put('--', addr)
        .then((response) => {
          if (response.status == 200) {
            //  addressBook 
            this.getAddressBook(addr.member_id);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    //   
    deleteAddress(id, userid) {
      // @@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@ TODO:   url 
      http
        .delete('--/' + id)
        .then((response) => {
          if (response.status == 200) {
            //  addressBook 
            this.getAddressBook(userid);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
