import { createRouter, createWebHistory } from 'vue-router';

// Views imports
// Home
import Home from '@/views/Home.vue';
// 
import Login from '@/views/User/Login.vue';
import Join from '@/views/User/Join.vue';
import MyPage from '@/views/User/MyPage.vue';
import UserModify from '@/views/User/UserModify.vue';
import SearchId from '@/views/User/SearchId.vue';
import SearchPassword from '@/views/User/SearchPassword.vue';
// 
import CartView from '@/views/Cart/CartView.vue';
// 
import OrderView from '@/views/Order/OrderView.vue';

// 
import AdminProfile from "@/views/Admin/AdminProfile.vue";
import AdminUserRegisterDetail from '@/views/Admin/AdminUserRegisterDetail.vue'
import AdminUserList from "@/views/Admin/AdminUserList.vue";
import AdminItemList from "@/views/Admin/AdminItemList.vue";
import AdminItemRegister from '@/views/Admin/AdminItemRegister.vue'

// 
import CSQna from '@/views/CS/CSQna.vue';
// 
import ItemList from '@/views/Item/ItemList.vue';
import ItemDetail from '@/views/Item/ItemDetail.vue';

// JWT-common import
// import JWTcommon from '@/utils/JWT-common'

// import mapGetters (admin) 
// import store from '@/store'

const routes = [
  // Home
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  /* ------------------------  ------------------------ */
  // 
  {
    path: '/user/login',
    name: 'Login',
    component: Login,
  },
  // 
  {
    path: '/user/join',
    name: 'Join',
    component: Join,
  },
  // MyPage
  {
    path: '/user/mypage',
    name: 'MyPage',
    component: MyPage,
    meta: {
      requiresAuth: true,
    },
  },
  //   
  {
    path: '/user/modify',
    name: 'UserModify',
    component: UserModify,
    meta: {
      requiresAuth: true,
    },
  },
  //  
  {
    path: '/user/search-id',
    name: 'SearchId',
    component: SearchId,
  },
  //  
  {
    path: '/user/search-password',
    name: 'SearchPassword',
    component: SearchPassword,
  },
  /* ------------------------ @ ------------------------ */
  // 
  {
    path: '/cart',
    name: 'CartView',
    component: CartView,
  },
  {
    path: '/order',
    name: 'OrderView',
    component: OrderView,
    meta: {
      requiresAuth: true,
    },
  },
  /* ------------------------  ------------------------ */
  //   Home -  
  {
    path: '/admin/profile',
    name: 'AdminProfile',
    component: AdminProfile,
    meta: {
      requiresAuth: true,
      requiersAdmin: true,
    },
  },
  //   
  {
    path: "/admin/user-register-detail",
    name: "AdminUserRegisterDetail",
    component: AdminUserRegisterDetail,
    meta: {
      requiresAuth: true,
      requiersAdmin: true,
    },
  },
  //  
  {
    path: '/admin/user-list',
    name: 'AdminUserList',
    component: AdminUserList,
    meta: {
      requiresAuth: true,
      requiersAdmin: true,
    },
  },
  //  
  {
    path: '/admin/item-list',
    name: 'AdminItemList',
    component: AdminItemList,
    meta: {
      requiresAuth: true,
      requiersAdmin: true,
    },
  },
  //  
  {
    path: "/admin/item-register",
    name: "AdminItemRegister",
    component: AdminItemRegister,
    meta: {
      requiresAuth: true,
      requiersAdmin: true,
    },
  },
  /* ------------------------  ------------------------ */
  // Home -   
  {
    path: "/cs/cs-qna",
    name: "CSQna",
    component: CSQna,
  },
  /* ------------------------  ------------------------ */
  //  
  {
    path: '/item/item-list',
    name: 'ItemList',
    component: ItemList,
  },
  //  
  {
    path: '/item/item-detail',
    name: 'ItemDetail',
    component: ItemDetail,
    // props: true, // for params
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});



// navigation guard
// 1.       
// 2. admin         

