import axios from 'axios'
import router from '@/router'
// JWT   
import JWTcommon from "@/utils/JWT-common";

// index.js import 
export const userStore = {
  namespaced: true,
  state: {
    //     JWT  
    // persistedState ?
    user: {},
    isAuthenticated : false,
  },
  mutations: {
    SET_AUTH(state, data) {
      // userid, name, phone, address  
      state.user = data.data
      state.isAuthenticated = true
      JWTcommon.saveTokens(data.auth_token)
      console.log('', state.user, state.isAuthenticated)
    },
    DESTROY_AUTH (state) {
      state.user = {}
      state.isAuthenticated = false
      JWTcommon.destroyTokens()
      console.log('', state.user, state.isAuthenticated)
    },
  },
  actions: {
    register ({dispatch}, credentials) {
      console.log('', credentials)
      axios({
        //     
        method: 'POST',
        url: 'http://localhost/member/regist',
        data: {
          member_userid: credentials.userid,
          member_userpwd: credentials.userpwd,
          member_name: credentials.name,
          member_phone: credentials.phone,
          member_address: credentials.address,
          member_type: credentials.type,
        }
      })
      .then((res) => {
        dispatch("login", credentials)
        console.log('', res)
      })
      .catch((err) => {
        alert(err)
      })
        .then((res) => {
          commit('SET_AUTH', res.data);
          console.log(res);
        })
        .catch((err) => {
          alert(err);
        });
    },

    login ({commit}, credentials) {
      console.log('', credentials)
      axios({
        method: 'POST',
        url: 'http://localhost/member/login',
        data: {
          member_userid: credentials.userid,
          member_userpwd: credentials.password
        }
      })
      .then((res) => {
        console.log(' ')
        commit("SET_AUTH", res.data)
        router.push({ name: "Home" })
      })
      .catch((err) => {
        console.log(err)
      })
    },
    logout({commit, state}) {
      console.log(state)
      axios({
        url: `http://localhost/member/logout/${state.user.member_userid}`
      })
      .then((res) => {
        commit("DESTROY_AUTH")
        console.log(res)
      })
    },
  },
  getters: {
    isAuthenticated(state) {
      return state.isAuthenticated
    },
    isAdmin(state) {
      return state.user.member_type === '' 
    }
  },
  modules: {},
};
